export default {
	data(){
		return{
			partyAddress:'',
			managerPhone:'',
			manager:'',
			partyTitle:'',
			signUrl:'',
			date:'2019年9月5日',
			titleContent:[{
				title:'甲方：',
				content:'成都凡果科技有限公司'
			},{
				title:'法人：',
				content:'龚果'
			},{
				title:'地址：',
				content:'成都市郫都区金融中心B栋11楼10号'
			}]
		}
	},
	onLoad() {
		this.getStorage()
	},
	methods:{
		onSign(){
			uni.navigateTo({
				url:'sign'
			})
		},
		onSubmit(){
			if (this.signUrl == '') {
				uni.showToast({
					title:'请签名',
					icon:'none'
				})
			} else{
				uni.showToast({
					title: '提交成功',
				})
				setTimeout(()=>{
					uni.navigateTo({
						url:'setting-key'
					})
				}, 1000)
			}
		},
		getStorage(){
			uni.getStorage({
				key:'signUrl',
				success:res=>{
					this.signUrl = res.data
				}
			})
			uni.getStorage({
				key:'partyTitle',
				success:res=>{
					this.partyTitle = res.data
				}
			})
			uni.getStorage({
				key:'manager',
				success:res=>{
					this.manager = res.data
				}
			})
			uni.getStorage({
				key:'managerPhone',
				success:res=>{
					this.managerPhone = res.data
				}
			})
			uni.getStorage({
				key:'partyAddress',
				success:res=>{
					this.partyAddress = res.data
				}
			})
		}
	}
}
